.CountryList {
  display: none;
  @media screen and (min-width: 992px) {
    display: block;
    max-height: 855px;
    overflow-y: scroll;
    direction: rtl;
    margin-right: -50px;

    &::-webkit-scrollbar {
      box-shadow: inset 0 0 5px grey;
      border-radius: 10px;
      background-color: transparent;
    }
    &::-webkit-scrollbar-thumb {
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 10px;
      height: 100px;
      background: linear-gradient(180deg, rgba(255, 228, 133, 0.5) 21.88%, rgba(186, 123, 0, 0.5) 100%);
    }
  }

  .error,
  .loading {
    height: 855px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
  }

  .list {
    @media screen and(max-width: 425px) {
      display: block;
    }
    direction: ltr;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    margin-left: 5px;

    .list__item {
      @media screen and(max-width: 991px) {
        width: 45%;
        height: 120px;
      }
      @media screen and(max-width: 425px) {
        width: 100%;
        height: 120px;
      }
      display: flex;
      margin-right: 5px;
      margin-bottom: 22px;
      height: 196px;
      background: linear-gradient(180deg, rgba(255, 228, 133, 0.5) 21.88%, rgba(186, 123, 0, 0.5) 100%);
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
      border-radius: 15px;
      cursor: pointer;

      .item__flag {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 60%;
        height: 100%;

        .flag__image {
          width: 72%;
          height: 73%;
          object-fit: cover;
          @media screen and(min-width: 992px) {
            height: 44%;
            margin-right: 20px;
          }
          @media screen and(min-width: 1440px) {
            height: 60%;
          }
        }
      }

      .item__info {
        width: 45%;
        margin-left: -20px;
        @media screen and(max-width: 991px) {
          width: 40%;
        }
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        overflow: hidden;
        text-overflow: ellipsis;

        .info__country {
          position: relative;
          width: 95%;

          .info__title {
            font-family: Roboto;
            font-style: normal;
            font-weight: 300;
            font-size: 10px;
            line-height: 12px;
            color: #575757;
            text-align: left;
          }

          .info__data {
            font-family: Roboto;
            font-weight: 900;
            font-size: 32px;
            line-height: 37px;
            color: #313131;
            max-height: 36px;
            overflow: hidden;
            text-overflow: ellipsis;
            @media screen and(max-width: 991px) {
              font-size: 16px;
              line-height: 17px;
              color: #313131;
              max-height: 18px;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
        }

        .info__capital,
        .info__region {
          @media screen and (min-width: 992px) {
            margin-top: 10px;
          }

          .info__title {
            font-family: Roboto;
            font-style: normal;
            font-weight: 300;
            font-size: 9px;
            line-height: 11px;
            color: #313131;
          }

          .info__data {
            font-family: Roboto;
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 28px;
            color: #313131;
            overflow: hidden;
            height: 26px;
            text-overflow: ellipsis;
            @media screen and(max-width: 991px) {
              font-size: 16px;
              line-height: 17px;
              color: #313131;
              max-height: 18px;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
        }
      }
    }
  }
}
